import React from "react";

const Figure = ({ alt, src }) => (
  <figure className='card' style={{ width: 200 }}>
    <img className='card-img-top' alt={alt} src={src} />
    <div className='card-body'>
      <figcaption className='card-text'>{alt}</figcaption>
    </div>
  </figure>
);

Figure.defaultProps = {
  alt: "a dog",
  src:
    "data:image/jpeg;base64,",
};

export default Figure;
